.card{
    height: 50vh;
    width: 30vw;
    margin-top: 20px;
    margin-left: 20px;
    border: 1px solid black;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
    background-color: rgb(247, 241, 241);
    border-radius: 10%;
}
.card-container{
    width: 80vw;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
}
.card:hover{
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}
img{
    width: 15vw;
    height: 25vh;
    border-radius: 50%;
}
button{
    padding: 10px;
    background-color: rgb(75, 75, 171);
    color: whitesmoke;
}
a{
    color: white;
    text-decoration: none;
}