.side-navbar {
    position: sticky;
    top:0;
    width: 15vw;
    height: 100vh;
    background-color: rgba(31, 40, 44, 0.718);
}

.side-navbar-items {
    width: 85%;
    padding: 10px;
    margin-top: 5px;
    font-size: 1.6rem;
    font-weight: 800;
    cursor: pointer;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    gap: 10px;
}

.side-navbar-items-icon {
    height: 7vh;

}
.side-navbar-items-name{
    color: black;
    text-decoration: none;
}

.side-navbar-items:hover {
    color: white;
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}
.side-navbar-items-name:hover{
    color: whitesmoke;
}
